<template>
  <div :style="{height:height*lineNum + 'px'}" class="rollScreen_container" id ="rollScreen_container">
    <ul class="rollScreen_list" :style = {transform:transform} :class="{rollScreen_list_unanim:num===0}">
      <li class="rollScreen_once" v-for="(item,index) in contentArr" :key=item :style="{height:height+'px'}">
        <span><img src="@/assets/logo.png" /></span>
        <span>{{item}}</span>
        <span>0.01</span>
        <span><br>舍三途苦，灭诸烦恼，开彼智慧！</span>
      </li>
      <li class="rollScreen_once" v-for="(item,index) in contentArr" :key=index+contentArr.length :style="{height:height+'px'}">
        <span><img src="@/assets/logo.png" /></span>
        <span>{{item}}</span>
        <span>0.01</span>
        <span><br>舍三途苦，灭诸烦恼，开彼智慧！</span>
      </li>
    </ul>
  </div>
</template>
 
<script>
export default {
  name: 'colswipper',
  props: {
    height: {
      default: 60,
      type: Number
    },
    lineNum: {
      default: 8,
      type: Number
    }
  },
  data: function () {
    return {
      contentArr: ['名字1', '名字2', '名字3', '名字4', '名字5', '名字6', '名字7','名字8', '名字9', '名字10', '名字11', '名字12'],
      num: 0
    }
  },
  computed: {
    transform(){
      return 'translateY(-' + this.num * this.height + 'px)'
    }
  },
  created() {
    let _this = this
    setInterval(()=> {
      if (_this.num !== _this.contentArr.length) {
        _this.num++
      } else {
        _this.num = 0
      }
    }, 3000)
  }
}
</script>
 
<style lang="less" scoped>
  .rollScreen_container{
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 70vw;
    font-size: 3.544vw;
  }
  .rollScreen_list{
    transition: 0.5s linear;
  }
  .rollScreen_list_unanim{
    transition: none
  }
  .rollScreen_once{
    display: flex;
    align-items: center;
    span{
      height: 17.667vw;
      line-height: 17.667vw;
      margin-left: 4.167vw;
      color:rgb(204, 197, 115);
    }
    :first-child{
      margin-left: 0;
    }
    :nth-child(2){
      overflow: hidden;
      width: 25vw;
    }
    img{
      width: 5.556vw;
      height: 5.556vw;
      border-radius: 50%;
      vertical-align: -1.111vw;
      background-color: rgb(204, 197, 115);
    }
    :last-child{
      line-height: 5.556vw;
    }
  }
</style>